import React from 'react';

function WorkItem(props) {

    function openPaperLink() {
        window.open(props.paperUrl, '_blank');
    }

    return (
        <div className="work-item-container">
            
            <h1><a href={props.url} target="blank">{props.title}</a></h1>
            <p>_id: {props._id}</p>
            <p>title: "{props.title}"</p>
            <p>position: "{props.position}"</p>
            <p>startDate: "{props.startDate}"</p>
            <p>endDate: "{props.endDate}"</p>
            <p>desc: Array</p>
            {props.paperUrl ? <p>researchPaper: <span className="clickable" onClick={openPaperLink}> "follow link!" </span> </p> : null}
            <p>[</p>
            <br/>
            <div className="job-desc">
                {props.desc.map((desc, index) => {
                    return (
                        <div className="work-info">
                            <p key={index}>"{desc}",</p>
                            <br/>
                        </div>
                    )
                })}
            </div>
            <p>]</p>
        </div>
    )
}

export default WorkItem;